<template>
  <div style="display:inline-block; height:30px;">
    <div id="box" @mouseenter="mouseenter" @mouseleave="mouseleave">
      <ul class="slogan-list" :class="{ anim: animate == true }">
        <li v-for="item in sloganposter" :key="item.id">
          <a :title="item.message">{{ item.message }}</a>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  name: "Notice",
  props: {
    sloganposter: {
      type: Array,
      default() {
        return [];
      }
    }
  },
  data() {
    return {
      animate: false
    };
  },
  created() {
    this.Interval();
  },
  methods: {
    mouseenter() {
      clearInterval(this.timer);
    },
    mouseleave() {
      this.Interval();
    },
    Interval() {
      this.timer = setInterval(() => {
        this.animate = true; // 因为在消息向上滚动的时候需要添加css3过渡动画，所以这里需要设置true
        setTimeout(() => {
          //  这里直接使用了es6的箭头函数，省去了处理this指向偏移问题，代码也比之前简化了很多
          this.sloganposter.push(this.sloganposter[0]); // 将数组的第一个元素添加到数组的
          this.sloganposter.shift(); // 删除数组的第一个元素
          this.animate = false; // margin-top 为0 的时候取消过渡动画，实现无缝滚动
        }, 1000);
      }, 3000);
    }
  }
};
</script>
<style scoped>
ul {
  margin: 0;
}
#box {
  width: 700px;
  height: 30px;
  overflow: hidden;
  display: inline-block;
  position: relative;
  top: 0px;
  margin-left: 30px;
}
.anim {
  transition: all 2s linear;
  margin-top: -58px;
}
.slogan-list li {
  list-style: none;
  line-height: 30px;
  height: 30px;
  width: 100%;
  white-space: nowrap;
  color: #fff;
  font-size: 16px;
  font-family: Helvetica Neue;
}
.slogan-list li a {
  color: #fff;
  font-family: 楷体;
  font-weight: bold;
}
</style>
